@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
.Card{position:relative;background-color:#fff;border-radius:.5rem;box-shadow:rgba(0,0,0,0.15) 0px 0.005rem 0.25rem;overflow:hidden;height:100%;cursor:pointer}.Card:hover{transition:all 0.5 ease;box-shadow:rgba(0,0,0,0.15) 0px 0.15rem 0.5rem}.Card__image{height:70%;position:relative;background-repeat:no-repeat;background-size:cover;background-position:center}.Card__imageBadge{position:absolute;bottom:0;left:0;width:100%;padding:.25rem 0;color:#fff;background-color:rgba(239,51,61,0.5);text-align:center}.Card__content{display:flex;align-content:space-between;flex-wrap:wrap;height:calc(30% - 2rem);padding:1rem;text-align:left}.Card__content>*{width:100%}

.Selector{display:block;position:relative;padding-left:3rem;margin-bottom:1rem;cursor:pointer;font-size:1.25rem;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Selector:hover .Selector__input ~ .Selector__checkmark{background-color:#d5d5d5}.Selector__input{position:absolute;opacity:0;height:0;width:0;cursor:pointer}.Selector__input:checked ~ .Selector__checkmark{background-color:#7e41b8;border-color:transparent}.Selector__input:checked ~ .Selector__checkmark:after{display:block}.Selector__checkmark{position:absolute;top:0;left:0;height:2rem;width:2rem;background-color:#eee;border-radius:.25rem;border:0.05rem solid #e6e6e6}.Selector__checkmark:after{content:"";position:absolute;display:none;left:0.7rem;top:0.3rem;width:0.4rem;height:0.8rem;border:solid #fff;border-width:0 0.25rem 0.25rem 0;transform:rotate(45deg)}

.ProductListItem{height:30rem}.ProductListItem__name{font-size:1rem}.ProductListItem__oldPrice{margin-left:.5rem;color:#ef333d;text-decoration:line-through}.ProductListItem__selector{position:absolute;top:1rem;left:1rem}

.ProductStock__stockQuantity{color:#2bb92b}.ProductStock__stockLow,.ProductStock__stockOut{font-size:.8rem;width:100%;text-transform:uppercase}.ProductStock__stockLow{color:#ffbd4e}.ProductStock__stockOut{color:#ef333d}

.ProductList{display:grid;grid-template-columns:18.75rem;grid-auto-rows:1fr;grid-gap:1rem;justify-content:center;padding:1rem}@media (min-width: 768px){.ProductList{grid-template-columns:repeat(3, 15rem)}}@media (min-width: 1200px){.ProductList{grid-template-columns:repeat(4, 15rem)}}

.Button{display:flex;align-items:center;justify-content:center;font-size:1rem;width:100%;max-width:15rem;color:#fff;background-color:#7e41b8;border-radius:.5rem;padding:.5rem;margin-bottom:.5rem;border:0;text-decoration:none;cursor:pointer}.Button:focus{outline:none}.Button:hover,.Button:active{background-color:#9764c8;border-color:#9764c8}

.Inventory{padding:2rem}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

*,*::before,*::after{margin:0;padding:0}html{font-family:'Lato', sans-serif;font-size:12px}body{margin:0;padding:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#f6f5fa}

